body {
  font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;
  font-size: 14px;
  line-height: 1.42857143;
  color: #333;
  background-color: #fff;
}

img
{
  margin-top: 18px;
  margin-left: 7px;
}
div {
  display: block;
}

.page-body{
  margin-left: 30px;
 
}

.admin-nav
{
  display: inline;
  margin-left: 20px;
}

.tiles-top
{
  margin-top: 20px;
}
.page-title {
  font-size: 26px;
  padding-top: 20px;
  margin-right: 100px;
}

.App {
  text-align: left;
}

.App-logo {
vertical-align: top;
}

.tile{
  display: inline-block;
  vertical-align: top;
  width: 220px;
  height: 190px;
  margin: 15px 15px 0 0;
  color: #ccc;
  position: relative;
  cursor: pointer;
  background: #24263c;
}


.tile-header{
  font-size: 12px;
  height: 30px;
}

.tile-header .dropdown {
  position: absolute;
  z-index: 2;
  right: 0;
}

.dropdown, .dropup {
  position: relative;
}

.tile-name {
  text-transform: uppercase;
  font-weight: 400;
  color: #fff;
  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  position: relative;
  bottom: 7px;
  font-size: 14px;
  text-align: center;
  position: relative;
  width: 100%;
}
.tile-body{
  height: 100px;
  width: 100%;
  overflow: hidden;
  text-align: center;
  position: relative;
  

}
.tile-footer{
  text-align: center;
  height: 20px;
  position: absolute;
  top: 10px;
  width: 100%;
  color: #999;
  position: relative;
  
}
.tile-type {
  text-transform: uppercase;
  position: absolute;
  left: 10px;
  top: 10px;
  color: #999;
}

.tile .kpi-points {
  margin: 10px 0 20px;
  width: 100%;
  font-size: 40px;
  line-height: 40px;
}

.kpi-points {
  color: #fff;
  text-align: center;
  font-size: 30px;
  margin: 0px 5px 0px;
}

.kpi-change {
  text-align: center;
color: #fff;
}
.App-header {
  background-color: #24263c;
  height: 60px;
}

.Bar-main{

  position: relative;
  color: white;
  font-size: 16px;
  vertical-align: middle;
  margin: 0;
  border: 0;
 -webkit-box-shadow: 0 1px 1px 1px rgb(0, 0, 0.3);
         box-shadow: 0 1px 1px 1px rgb(0, 0, 0.3);
}

.Bar-listing
{
  position: relative;  
  display: inline-block;
  cursor: pointer;
  padding-left: 20px;  
  padding-right: 20px;
  padding-top: 20px;
  padding-bottom: 20px;
  min-width: 120px;
  text-align: center;
 
}

.innercontents
{
  display: none;
  position: absolute;
  max-height: 75vh;
  overflow: auto;
    top: 100%;
    left: 0;
    z-index: 1000;
    display: none;
    float: left;
    min-width: 160px;
    padding: 5px 0;
    margin: 2px 0 0;
    font-size: 14px;
    text-align: left;
    list-style: none;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ccc;
    border: 1px solid rgba(0,0,0,.15);
    border-radius: 4px;
    -webkit-box-shadow: 0 6px 12px rgba(0,0,0,.175);
    box-shadow: 0 6px 12px rgba(0,0,0,.175);
    margin-top: -15px;
}


.innercontents a
{
  padding: 7.5px 15px;
  text-decoration: none !important;
  color: #24263c;
  display: block;
  background-color: white;
}

.innercontents a:hover
{
  padding: 7.5px 15px;
  text-decoration: none !important;
  color: #24263c;
  display: block;
  background-color: #F0F0F0;
}
.Bar-listing:hover
{
  background-color: white;
  color: #24263c;
}

.Bar-listing:hover .innercontents
{
  display: block;
}

.App-intro {
  font-size: large;
}

.dropdot {
  color:#fff;
  margin-right: 10px;
  margin-bottom: 8px;
  font-family: 'Glyphicons Halflings';
  font-style: normal;
  font-weight: 400;
  line-height: 1;
  font-size: 28px;
  text-align: right;
float: right;
}

.dotinner
{
  display: none;
  position: absolute;
  max-height: 75vh;
  overflow: auto;
    top: 100%;
    left: 0;
    z-index: 1000;
    display: none;
    float: left;
    min-width: 160px;
    padding: 5px 0;
    margin: 2px 0 0;
    font-size: 16px;
    text-align: left;
    list-style: none;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ccc;
    border: 1px solid rgba(0,0,0,.15);
    border-radius: 4px;
    -webkit-box-shadow: 0 6px 12px rgba(0,0,0,.175);
    box-shadow: 0 6px 12px rgba(0,0,0,.175);
    margin-top: -170px;
    margin-left: 190px;
}

.dropdot:hover .dotinner
{
  display: block;
  
}

.dotinner a
{
  display: block;
  padding: 3px 20px;
  
  background-color: transparent;
  clear: both;
  font-weight: 400;
  line-height: 1.42857143;
  color: #333;
  white-space: nowrap;
  text-decoration: none;
}
.dotinner a:hover
{
  
  background-color: #F0F0F0;
}

.dropdownheader
{
  display: block;
  padding: 3px 20px;
  font-size: 13px;
  line-height: 1.42857143;
  color: #777;
  white-space: nowrap;
  text-transform: uppercase;
}
.kpitc {
  background-color: white; 
  color: #337ab7;
  text-align: center;
  text-decoration: none;
  font-size: 14px;
  font-weight: bold;
  border: 1px solid #337ab7;
  text-align: center;
  cursor: pointer;
  height: 30px;
  text-transform: uppercase;
  min-width: 9%;
  overflow: auto;
}

.kpitc:hover {
 
  background-color: #337ab7; 
  color: white;
  height: 30px;
  width:9%;
 text-align: center;
  text-decoration: none;
  font-size: 14px;
  border: 1px solid transparent;
}

.kpitcright {
  background-color: white; 
  color: #337ab7;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  border: 1px solid #337ab7;
  text-align: center;
  cursor: pointer;
  width: 100px;
  height: 30px;
  float: right;

}

.kpitcright:hover {
 
  background-color: #337ab7; 
  color: white;
  width: 100px;
  height: 30px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  border: 1px solid transparent;
}
@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



